import { Component, Input, TemplateRef } from '@angular/core';
import { IOperation, IResultList } from '@c8y/client';
import { OperationRealtimeService } from '@c8y/ngx-components';
import {
  OperationsService,
  OperationStatusOptionsMap,
  OPERATION_STATUS_OPTIONS_MAP
} from '@c8y/ngx-components/operations/shared';
import { pipe, UnaryFunction } from 'rxjs';
import { tap } from 'rxjs/operators';

@Component({
  selector: 'c8y-operations-timeline',
  templateUrl: 'operations-timeline.component.html'
})
export class OperationsTimelineComponent {
  @Input()
  operations: IResultList<IOperation>;

  @Input()
  sourceId: string;

  @Input()
  filterPipe: UnaryFunction<unknown, unknown> = pipe(tap());

  @Input()
  bodyTemplate: TemplateRef<any>;

  @Input()
  footerTemplates: Array<TemplateRef<any>> = [];

  @Input()
  propertiesToHide: string[] = [];

  OPERATION_STATUS_OPTIONS_MAP: OperationStatusOptionsMap = OPERATION_STATUS_OPTIONS_MAP;

  constructor(
    public realtime: OperationRealtimeService,
    public operationsService: OperationsService
  ) {}
}
